import React from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";
import { BiCrown, BiSolidBot } from "react-icons/bi";

const HeaderWrapper = styled.header`
  height: 100px;
  background-color: ${({ theme }) => theme.colors.headerBg};
`;

const HeaderInner = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 0 20px;
  max-width: 1240px;
  height: 100%;
`;

const Logo = styled(Link)`
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 1rem;
  font-weight: bold;

  svg {
    font-size: 1.5rem;
  }
`;

const Message= styled.div`
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.8rem;
  color: red;
`;

const Header = () => {
  return (
    <HeaderWrapper>
      <HeaderInner>
        <Logo to="/"><BiCrown /> </Logo>
        <Message><BiSolidBot />        .        ,   .</Message>
      </HeaderInner>
    </HeaderWrapper>
  );
};

export default Header;